<template>
  <a-row type="flex" justify="end" style="min-width: 1400px">
    <a-col style="width: 85%">
      <div :style="{ background: '#fff', padding: '24px', minHeight: '280px' }">
        <a-row type="flex" justify="start">
          <a-col :span="3">
            <router-link to="/farmerList">
            <a-button type="link"><a-icon type="left" />返回</a-button>
            </router-link>
          </a-col>
        </a-row>
        <a-row type="flex" justify="start" style="margin-top:30px ; margin-left:5px">
          <a-col :span="3" offset="1">
            <h2 style="font-weight: bold">{{groupData.leader.name}} 工长</h2>
          </a-col>
          <a-col :span="2" offset="16">
            <h2 :style="machineStatusStyle(groupData.authState)">{{groupData.authState}}</h2>
          </a-col>
        </a-row>
        <a-row :gutter="12" type="flex" justify="start">
          <a-col :span="15">
            <a-form id="components-form-demo-validate-other" :form="form">
              <a-form-item label="身份证号" :labelCol="{ span: 4 }" :wrapperCol="{ span: 14 }">
                <span class="ant-form-text">{{groupData.leader.IDCard}}</span>
              </a-form-item>
              <a-form-item label="联系电话" :labelCol="{ span: 4 }" :wrapperCol="{ span: 14 }">
                <span class="ant-form-text">{{groupData.leader.phonenumber}}</span>
              </a-form-item>
              <a-form-item label="开卡银行" :labelCol="{ span: 4 }" :wrapperCol="{ span: 14 }">
                <span class="ant-form-text">{{groupData.leader.Bank}}</span>
              </a-form-item>
              <a-form-item label="银行卡号" :labelCol="{ span: 4 }" :wrapperCol="{ span: 14 }">
                <p class="ant-form-text">{{groupData.leader.BankNumber}}</p>
              </a-form-item>
            </a-form>
          </a-col>
        </a-row>
        <a-card style="margin: 24px 48px 24px 48px" :bordered="true" :title="groupData.type+groupData.classNumber+'组'">
          <a href="#" slot="extra" @click="exportExcel">一键下载资源</a>
            <a-table :columns="columns" :dataSource="groupList" :pagination="{ pageSize: 6 }"></a-table>
        </a-card>
        <a-layout-footer style="textAlign: center">DDDB ©2019 Created by 六芒星</a-layout-footer>
      </div>
      <a-affix :offsetBottom="0">
        <div class="affix-bottom">
          <a-form layout="inline" @submit="handleSubmit" :form="form">
            <a-form-item style="text-align: center">
              <a-button htmlType="submit" type="primary" @click="postAuthResult('审核未通过')">驳回</a-button>
            </a-form-item>
            <a-form-item style="text-align: center ">
              <a-button htmlType="submit" type="primary" @click="postAuthResult('审核通过')">通过</a-button>
            </a-form-item>
          </a-form>
        </div>
      </a-affix>
    </a-col>
  </a-row>
</template>
<script>
import { getGroupList,postGroupAuthInfo } from "../../api/manRequest";
const columns = [
  {
    title: "成员姓名",
    dataIndex: "name",
    key: "name",
    width: "20%"
  },
  {
    title: "身份证号",
    dataIndex: "IDCard",
    key: "IDCard",
    width: "40%"
  },
  {
    title: "联系方式",
    dataIndex: "phoneNumber",
    key: "phoneNumber",
    width: "40%"
  }
];
export default {
  components: {},
  data() {
    return {
      columns,
      id: localStorage.getItem("userId"),
      groupId: Number(this.$route.query.groupId),
      groupData: {},
      groupList: []
    };
  },
  computed: {
    list() {
      // return this.$store.state.taskList;
    },
    paginationProps() {
      let pag = {
        pageSize: 6,
        total: this.data.length
      };
      return pag;
    },
    data() {
      let list = [...this.groupList];
      return list;
    }
  },
  mounted() {
    this.getGroupList();
  },
  created() {
    this.$parent.showMenu = false;
    this.$parent.showSiderMenu = true;
  },
  methods: {
    //动态样式
    machineStatusStyle(e){
      if(e == '审核中'){
        return {
          'color':'#D39846'
        }
      }else if(e == '审核未通过'){
        return {
          'color':'#ABABAA'
        }
      } else if(e == '审核通过'){
        return {
          'color':'#4F5F46'
        }
      };
    },
    getGroupList() {
      getGroupList(this.groupId)
        .then(response => {
          this.groupData = response.data.group_data;
          this.groupList = response.data.members;
          console.log(this.groupData);
          console.log(this.groupList);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    postAuthResult(isPass){
      const postAuthInfo = {id:this.groupId,auth_result:isPass };
      const that = this;
      postGroupAuthInfo(postAuthInfo)
        .then(function(response) {
          console.log(response);
          if (response.data.result === 25) {
            alert("审核成功");
            that.$router.push({ path: "/farmerList" });
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    exportExcel() {
            import('../../excle/Export2Excel').then(excel => { // 导入js模块
                const tHeader = ['编号', '成员姓名', '身份证号', '联系方式','资质信息','所属组号']; // 导出excel 的标题
                const filterVal = ['id','name', 'IDCard', 'phoneNumber','authInfo','group']; // 每个标题对应的字段

                const list = (this.groupList || []).map((item, key) => { // 通过 map 方法遍历，组装数据成上面的格式
                    return {
                        id: item.id,
                        name: item.name,
                        IDCard: item.IDCard,
                        phoneNumber: item.phoneNumber,
                        authInfo: 'https://www.dddianbing.com' + item.authInfo,
                        group: item.group
                    };
                });

                if (list) {
                    const data = this.formatJson(filterVal, list); // 生成json数据
                    excel.export_json_to_excel( // 调用excel方法生成表格
                        tHeader,
                        data,
                        this.groupData.leader.name + '工长的成员信息'
                    );
                } else {
                    alert('暂无无数据');
                }
                // this.loading = false;
            })
        },
        formatJson (filterVal, jsonData) {
            return jsonData.map(v => filterVal.map(j => v[j]));
        }
  }
};
</script>

<style lang="less" scoped>
.card-list {
  /deep/ .ant-card-body:hover {
    .ant-card-meta-title > a {
      color: #f5222d;
    }
  }

  /deep/ .ant-card-meta-title {
    margin-bottom: 12px;

    & > a {
      display: inline-block;
      max-width: 100%;
      color: rgba(0, 0, 0, 0.85);
    }
  }

  /deep/ .meta-content {
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    height: 64px;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;

    margin-bottom: 1em;
  }
}

.affix-bottom {
  background: #b80c0a;
  width: 100%;
  margin: 0 auto;
  height: 80px;
  padding: 20px 0 0 20px;
}
.ant-card-actions {
  background: #f7f9fa;

  li {
    float: left;
    text-align: center;
    margin: 12px 0;
    color: rgba(0, 0, 0, 0.45);
    width: 50%;

    &:not(:last-child) {
      border-right: 1px solid #e8e8e8;
    }

    a {
      color: rgba(0, 0, 0, 0.45);
      line-height: 22px;
      display: inline-block;
      width: 100%;
      &:hover {
        color: #f5222d;
      }
    }
  }
}

.new-btn {
  background-color: #fff;
  border-radius: 2px;
  width: 100%;
  height: 188px;
}

.vcenter {
  position: absolute;
  left: 50%;
  top: 50%;
}
.listpage {
  min-height: 50px;
  background: #fff;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 16px 10px 0px 10px;
  width: 100%;
}
</style>



